.VaeonLogo {
  width: 450px;
  padding: 40px;
}

#VaeonLogo-logo {
  -webkit-animation: ease-in-out hvr-bob-top 3.5s infinite;
          animation: ease-in-out hvr-bob-top 3.5s infinite;
}

@-webkit-keyframes hvr-bob-top {
  0% {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
    fill: #647dfe;
  }

  50% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    fill: black;
  }

  100% {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
    fill: #647dfe;
  }
}

@keyframes hvr-bob-top {
  0% {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
    fill: #647dfe;
  }

  50% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    fill: black;
  }

  100% {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
    fill: #647dfe;
  }
}



